var gtransfer
var listener 
/**
 * 获取字体变化实例，单例模式
 * @param {*} oaCount 
 * @param {*} appKey 
 */
const getListener = function(oaCount, appKey, options){
    if(listener){
        return listener
    }
    if(window.createCopyListener){
        return window.createCopyListener(oaCount, appKey, options)
    }
}
/**
 * 获取更新函数，单例模式
 * @param {*} oaCount 
 * @param {*} appKey 
 */
const getGtransfer = function(oaCount, appKey){
    if(gtransfer){
        return gtransfer
    }
    // 防止调用失败，如果不存在gtransfer,则再生成一个
    const lis = getListener(oaCount, appKey)
    if(lis){
        lis.connect().then(function(transfer){
            gtransfer = transfer
        }).catch((msg) => {
            console.log('报错了')
            console.log(msg)
        })
        return gtransfer
    }
}

/**
 * 数据更新后更新water
 * @param {*} el 
 * @param {*} binding 
 */
const updateWater = function (el, binding, vnode, oaCount, appKey, options){
    const text = binding.value
    const gtrans = getGtransfer(oaCount, appKey, options)
    if(gtrans && typeof gtrans == 'object'){
        gtrans.update()
        var unicodes = gtrans.transform(text)
        el.innerHTML = unicodes
    }
}

/**
 * 初始化指令
 * @param {*} oaCount 
 * @param {*} appKey 
 * @param {*} options 
 */
const initCopy = function (oaCount, appKey, options){
    listener = getListener(oaCount, appKey, options)
    listener.connect().then(function(transfer){
        gtransfer = transfer
    }).catch((msg) => {
        console.log('报错了')
        console.log(msg)
    })
    return listener
    // Vue.prototype.$copy = listener
}

const checkObj = function (obj) {
    return Object.prototype.toString.call(obj) == '[object Object]'
}

const formatText = function (text){
    const reg = /<\/?.+?\/?>/g
    const textSplit = text.split('&#x')
    return textSplit.map(function (item){
        console.log(reg.test(item))
        if(!reg.test(item)){
            return '\\u' + item + ';'
        }
        return item
    }).join('')
}

let createCopy = (function(){
    return function (oaCount, appKey, options, Vue){
        if (!oaCount || !appKey){
            console.log('请传入oaCount,appKey')
            return
        }
        if(!checkObj(options)){
            options = {}
        }
        Vue.prototype.$copy = initCopy(oaCount, appKey, options)
    
        Vue.directive('maya-copy', {
            bind(el, binding){
                const text = binding.value
                var unicodes = gtransfer.transform(text)
                el.innerHTML = unicodes
            },
            componentUpdated(el, binding, vnode, oaCount, appKey, options){
                updateWater(el, binding, vnode, oaCount, appKey, options)
            },
        })
        // Vue.filter('maya-copy', function(value){
            
        //    if(!value){
        //        return value
        //    } 
        //    var unicodes = gtransfer.transform(value)
        //    return formatText(unicodes)
        // }) 
    }
})()

module.exports = createCopy 
